@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
* {
  box-sizing: border-box;
  font-family: "Lato", sans-serif;
}
.Container {
  margin: 0 auto;
  max-width: 1170px;
}

.links {
  text-align: right;
  margin: 10px 0;
}
.link__item {
  margin: 15px;
  text-decoration: none;
  color: black;
  font-size: 16px;
  font-family: "Lato", sans-serif;
}
.link__item:last-child {
  margin-right: 0;
}
.header {
  display: flex;
  align-items: center;
}
.header__items {
  display: flex;
  background-color: #f7f7f7;
  width: 100%;
  margin-left: 2%;
  justify-content: right;
}

.header__item {
  margin-left: 5px;
  align-items: center;
  text-align: center;
  padding: 40px 25px;
  text-decoration: none;
  color: white;
  font-size: 16px;
  font-family: "Lato", sans-serif;
}

.header__item:nth-child(odd) {
  background-color: #1eb6d1;
}
.header__item:nth-child(even) {
  background-color: #f1717c;
}

.header__item:nth-child(3),
.header__item:nth-child(4) {
  display: flex;
}

.header_item_image {
  margin-left: 12px;
}

.nav {
  margin: 15px 0;
}

.nav__item {
  padding: 30px;
  cursor: pointer;
  color: white;
}

.nav__item:hover {
  color: #ef5966;
  background-color: white;
}
.nav a {
  display: block;
  text-decoration: none;
  outline: none;
  transition: 0.4s linear;
}
.menu {
  background-color: #05aecc;
  display: flex;
  justify-content: space-around;
}
.menu li {
  display: inline-block;
  position: relative;
}

.submenu {
  margin: 0;
  padding: 0;
  background-color: white;
  position: absolute;
  left: 0;
  top: 100%;
  visibility: hidden;
  opacity: 0;
  z-index: 5;
  width: 260px;
  transform: perspective(600px) rotateX(-90deg);
  transform-origin: 0% 0%;
  transition: 0.6s ease-in-out;
}

.submenu li {
  display: flex;
}

.menu > li:hover .submenu {
  visibility: visible;
  opacity: 0.9;
  transform: perspective(600px) rotateX(0deg);
}

.submenu_flex p {
  font-size: 14px;
}

.item__name {
  color: #ef5966;
  letter-spacing: 1.2px;
}
.item__btn {
  color: white;
  background-color: #05aecc;
  text-align: center;
  padding: 6px 0;
  width: 117px;
  margin-top: 5px;
  font-size: 12px;
}

.item__btn:hover {
  color: lavender;
}

.li__item {
  display: flex;
  padding: 19px;
  border-bottom: 1px solid #cacaca;
  padding-left: 20px;
}
.submenu_flex {
  margin-left: 10px;
}
.item__text {
  font-size: 14px;
  color: black;
}

.seeMore {
  color: #ef5966;
  margin: 15px;
}

